body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Larish Neue";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button:focus {outline:0 !important;}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.buttons-line-container {
    margin: 50px auto 0;
    width: 100vw;
    -webkit-transition: .2s;
    transition: .2s;
    background-color: #004466;
}

.buttons-line-container-smaller {
    margin: 0;
    width: 100vw;
    -webkit-transition: .2s;
    transition: .2s;
    height: 60px;
    background-color: #004466;
}


.Home-content-name {
    -webkit-transition: 0.5s;
    transition: 0.5s;
    position: fixed;
    height: 100px;
    /* margin-top: -10px; */
    background-color: #004466;
    -webkit-transition: .5s;
    transition: .5s;
}

/* .Home-content-name-smaller {
    transition: 0.5s;
    height: 0;
} */

.Home-content-name-smaller {
    position: fixed;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}


.Home-header{
    background-size: cover;
    background-color: #004466;
    margin-left: auto;
    margin-right: auto;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    position: fixed;
}

.Home-header-remove{
    -webkit-transition: 0.5s;
    transition: 0.5s;
    position: fixed;
}

.Home-header-text{
    font-size: 35px;
    width: 100vw;
    margin: -35px auto 0 10PX;
    /* background-color: #3f5866; */
    color:#ff2105;
    font-family: "Playfair Display";
    -webkit-transition: .5s;
    transition: .5s;
}

.Home-header-text-remove {
    -webkit-transition: .5s;
    transition: .5s;
    font-size: 35px;
    width: 100vw;
    margin: -100px auto 0 10PX;
    /* background-color: #3f5866; */
    color:#ff2105;
    font-family: "Playfair Display";
}


.Home-nav-buttons-after{
    color: #e4e5e6;
    background: transparent;
    cursor: pointer;
    height: 45px;
    width: 100px;
    border: none;
    font-style: italic;
    font-family: "voiceinmyhead";
    font-size: 25px;
    padding: 0 !important;
    position: relative; 
    -webkit-transition: .2s; 
    transition: .2s; 
}

.Home-nav-buttons-after:hover {
    color: #ff2105;
    -webkit-transition: 1s;
    transition: 1s;
}

.Home-nav-buttons-after-remove {
    color: #00000000;
    background: transparent;
    border: none;
    -webkit-transition: .2s;
    transition: .2s;
}
@media (max-width: 600px){    
    .Home-header-text{
        font-size: 20px;
        margin-left: auto;
        margin-right: auto;
    }

    .map-a-tag{
        width: none;
        max-width: 90%;
        min-width: 90%;
    }
    
    .text-contact-container div{
        font-size: 25px;
    }
}
.background-image-app{
    background-image: url(/avdr/static/media/LA.ca77e2f4.jpg);
    background-size: cover;
    position: fixed;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
    z-index: -99999999999999999;
  }
  
.bottom-block{
    background-color: #e4e5e6;
}

.center{
    text-align: center;
    display: block;
    font-size: 15px;
    font-family: "Playfair Display";
    color: #e4e5e6;   
}

.description {
    max-width: 80vw;
    margin-left: auto;
    margin-right: auto;
    font-size: 25px;
    font-family: "Playfair Display";
    color: #004466;
}

.text-block{
    background-color: #004466;
    margin: auto;   
}

.home-screen{
    height: 100vh;
    background-color: #004466;
}

.map-a-tag{
    z-index: 999999;
    display: block;
    margin: auto;
    width: 50%;
}

.map-block{
    margin: auto;
    width: 100%;
    height: auto;
    display: block;
    cursor: pointer;
}

.map-container{
    text-align: center;
}

.map-tag-container{
    position: relative;
}

.pic-block{
    /* height: 60vh; */
    padding-top: 100px;
    background-color: #e4e5e6;
}

.under-header{
    position: absolute;
}
.contact-image{
    width: 100%;
    margin: auto;
    display: block;
}

.align{
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%);
}

.contact-center{
    text-align: center;
}

.credit-container{
    position: relative;
}

.credit-photo{
    float: bottom;
    font-size: 10px;
    position: absolute;
    bottom: 0;
}

.image-background-contact{
    background-image: url(/avdr/static/media/contact.a894432b.jpg);
    width: 50%;
    height: auto;
    background-size: 50;
}

.text-contact{
    background-color: #e4e5e685;
    text-align: center;
}

.text-contact-container{
    width: 70%;
    background-image: url(/avdr/static/media/contact.a894432b.jpg);
    background-repeat: no-repeat;
    display: block;
    margin: auto;
    background-size: 100%;
    font-size: 40px;
}


body{
  font-family: "arial";
  background-color: #e4e5e6;
  color: #004466;
}
